/* ------------------JUMBOTRON SLIDER-------------------------------------------------------- */
.jumbotron{
 margin-bottom: 0;
 border-radius:0;
 background-position: center;
 background-repeat: no-repeat;
 background-size: cover;
 min-height: 85vh;
 /* padding:     7rem 9rem; */

 color: white;
}

.jumbotron h1{
  opacity:0;
  -webkit-transform: translatey(-40%);
          transform: translatey(-40%);
  -webkit-animation: 1s slidefadeYDown 0.5s forwards;
          animation: 1s slidefadeYDown 0.5s forwards;
}
@-webkit-keyframes slidefadeYDown {
    100% {
      opacity: 1;
      -webkit-transform: translatey(0%);
              transform: translatey(0%);
}
}
@keyframes slidefadeYDown {
    100% {
      opacity: 1;
      -webkit-transform: translatey(0%);
              transform: translatey(0%);
}
}

.jumbotron p{
  opacity:0;
  -webkit-transform: translatex(-40%);
          transform: translatex(-40%);
  -webkit-animation: 1s slidefadeXRight 1.5s forwards;
          animation: 1s slidefadeXRight 1.5s forwards;
}

@-webkit-keyframes slidefadeXRight {
    100% {
    opacity: 1;
    -webkit-transform: translatex(0%);
            transform: translatex(0%);
}
}

@keyframes slidefadeXRight {
    100% {
    opacity: 1;
    -webkit-transform: translatex(0%);
            transform: translatex(0%);
}
}

.jumbotron>a.btn{
  opacity:0;
  -webkit-transform: translatey(50%);
          transform: translatey(50%);
  -webkit-animation: 1s slidefadeXRight 2.5s forwards;
          animation: 1s slidefadeXRight 2.5s forwards;


}
a.btn{
  color: white;
  outline: none;
  background: linear-gradient(90deg, rgba(1,95,169,1) 22%, rgba(5,203,252,1) 54%, rgba(240,123,42,1) 91%);  outline:none;
  transition: background-image 3s;
}
a.btn:hover{
  outline:none;
  background-image: linear-gradient(to left, #2b56f5 0%, #46bdf4 100%);

}

@-webkit-keyframes slidefadeYUP {
    100% {
    opacity: 1;
    transform: translatey(0%);
    -webkit-transform: translatey(0%);
    -moz-transform: translatey(0%);
    -ms-transform: translatey(0%);
    -o-transform: translatey(0%);
}
}

@keyframes slidefadeYUP {
    100% {
    opacity: 1;
    transform: translatey(0%);
    -webkit-transform: translatey(0%);
    -moz-transform: translatey(0%);
    -ms-transform: translatey(0%);
    -o-transform: translatey(0%);
}
}


